import React from "react";
import {Avatar, Header, SimpleCell, Progress} from "@vkontakte/vkui";
import pivo from "../img/pv.png";
import $ from "jquery";
import Icon24Info from '@vkontakte/icons/dist/24/info';
import getParams from "../functions/getParams";
import pevkoCalc from "../functions/pevkoCalc";
import maxPevkoCapacity from "../functions/maxPevkoCapacity";

const Layout = (p) => {
    let name = p.data.name;
    let description = p.data.description;
    let owner = p.owner;
    let uid = parseInt(getParams('vk_user_id'));
    let isUID = (uid === owner.id);
    let pevko = pevkoCalc(p.data.value, p.data.taken);
    return (
        <div id="layout">
            <div
                id="layoutBG"
                style={{
                    backgroundImage: 'url('+p.data.background+')',
                    width: '100%',
                    height: '30vh',
                    backgroundAttachment: 'scroll',
                    backgroundClip: 'border-box',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat',
                    backgroundSize: 'cover'
                }}
            />
            <Header className="layoutName">{name}</Header>
            <SimpleCell before={<Icon24Info />}>{description}</SimpleCell>
            <SimpleCell before={<Avatar size={48} src={owner.photo_200} />} description="Владелец">{owner.first_name+' '+owner.last_name}</SimpleCell>
            <SimpleCell
                style={isUID ? {display: 'none'}: {}}
                onClick={p.buy}
                multiline={true}
                before={<img src={pivo} alt="певко" width={24} height={24}/>}
                expandable={true}
            >Переманить работяг за {pevkoCalc(p.data.value, p.data.taken)} певка
            </SimpleCell>
            <SimpleCell
                style={!isUID ? {display: 'none'}: {}}
                onClick={p.collect}
                multiline={true}
                before={<img src={pivo} alt="певко" width={24} height={24}/>}
                expandable={true}
                description={<Progress value={pevko/maxPevkoCapacity(p.data.value)*100 > 100 ? 100 : pevko/maxPevkoCapacity(p.data.value)*100} />}
            >Собрать {pevko > maxPevkoCapacity(p.data.value) ? maxPevkoCapacity(p.data.value) : pevko} певка
            </SimpleCell>
            <div
                onClick={() => {$('#layout').removeClass('active')}}
                style={{
                    position: 'absolute',
                    bottom: 15,
                    left: 'calc(50% - 36px)',
                    boxShadow: '2px 2px 10px -1px rgba(0,0,0,0.55)',
                    padding: 10,
                    borderRadius: 25
                }}>Закрыть</div>
        </div>
    )
}

export default Layout;